function copyImg() {

    let currentImg = document.querySelector('.single__mainbox--img').src;
    
    let newImg = document.createElement('img');
    newImg.classList = 'single__mainbox--img-mini';
    newImg.src = currentImg;

    document.querySelector('#child_img').appendChild(newImg);
    
    
    let currentPlan = document.querySelector('.single__mainbox--plan').src;
    let newPlan = document.createElement('img');
    newPlan.classList = 'single__mainbox--plan-mini';
    newPlan.src = currentPlan;
    console.log(newPlan);

    document.querySelector('#child_plan').appendChild(newPlan);

}

copyImg();

function switchImg() {
    
    let selectImg = document.querySelector('.single__mainbox--img-mini');
    let selectPlan = document.querySelector('.single__mainbox--plan-mini');
    let main = document.querySelector('.single__mainbox--img');
    let plan = document.querySelector('.single__mainbox--plan');

    selectImg.addEventListener('click', () => {
        plan.classList.remove('display');
        main.classList.add('display');
    })
    selectPlan.addEventListener('click', () => {
        main.classList.remove('display');
        plan.classList.add('display');
    })

}

switchImg();
